import React from 'react';
import { Panel, ListItem, Group, Avatar, PanelHeader, List, Cell } from '@vkontakte/vkui';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';

const Home = ({ id, fetchedUser, dataUser }) => (
	<Panel id={id}>
		<PanelHeader>Информация об участнике</PanelHeader>
		{fetchedUser &&
		<Group title="Информация">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={"Участник конкурса"}>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		{dataUser &&
		<Group title="Ваши баллы">
			<List>
				<Cell indicator={<Counter type="primary">{dataUser ? dataUser.p_likes : "-1"}</Counter>}>Лайки</Cell>
				<Cell indicator={<Counter type="primary">{dataUser ? dataUser.p_flikes : "-1"}</Counter>}>Лайки первым</Cell>
				<Cell indicator={<Counter type="primary">{dataUser ? dataUser.p_tlikes : "-1"}</Counter>}>Лайки в числе первых</Cell>
				<Cell indicator={<Counter type="primary">{dataUser ? dataUser.p_alllikes : "-1"}</Counter>}>Лайки всех постов за день</Cell>
				<Cell indicator={<Counter type="primary">{dataUser ? dataUser.p_gposts : "-1"}</Counter>}>Хорошие посты</Cell>
				<Cell indicator={<Counter type="primary">{dataUser ? dataUser.p_gcomm : "-1"}</Counter>}>Хорошие комментарии</Cell>
			</List>
		</Group>}
	</Panel>
);

export default Home;